<template>
  <div>
    <el-form :model="ruleForm"
      :rules="rules"
      ref="ruleForm"
      label-width="120px"
      class="demo-ruleForm">
      <el-form-item label="Full Name" prop="name">
        <el-input v-model="ruleForm.name"></el-input>
      </el-form-item>
      <el-form-item label="Email" required prop="email">
        <el-input v-model="ruleForm.email"></el-input>
      </el-form-item>
      <el-form-item label="No Handphone" required prop="no_handphone">
        <el-input v-model="ruleForm.phone"></el-input>
      </el-form-item>
      <el-form-item label="Message" prop="message">
        <el-input type="textarea" v-model="ruleForm.message"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm">Submit</el-button>
        <el-button @click="resetForm">Cancel</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        ruleForm: {
          name: '',
          email: '',
          no_handphone: 0,
          messsage: ''
        },
        rules: {
          name: [
            { required: true, message: 'Please input Activity name', trigger: 'blur' },
            { min: 3, max: 5, message: 'Length should be 3 to 5', trigger: 'blur' }
          ],
          email: [
            { required: true, message: 'Please input your email', trigger: 'change' },
            { min: 5, message: 'Length should be minimal 5', trigger: 'blur' }
          ],
          no_handphone: [
            { type: Number, required: true, message: 'Please pick a date', trigger: 'change' }
          ],
          message: [
            { required: true, message: 'Please input message', trigger: 'blur' }
          ]
        }
      };
    },
    methods: {
      submitForm () {
        this.$refs.formName.validate((valid) => {
          if (valid) {
            db.collection('message').add({
              full_name: this.ruleForm.name,
              email: this.ruleForm.email,
              price: this.ruleForm.no_handphone,
              no_handphone: this.ruleForm.messsage
            })
          } else {
            return false;
          }
        });
      },
      resetForm () {
        this.$refs.formName.resetFields();
      }
    }
  }
</script>
